* {
  font-family: 'Roboto', sans-serif;
  scroll-behavior: smooth;
}

.container {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  position: relative;
}

select {
  border: 0;
  margin-top: 20px;
  margin-right: 20px;
  margin-left: 20px;
  border-radius: 20px;
  font-size: 18px;
  height: 38px;
  width: 250px;
  text-align: center;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 1;
}

.ttt {
  position: fixed;
  width: 100%;
  left: 45%;
  bottom: 40px;
  height: 40px;
  font-size: 3rem;
  z-index: 999;
  cursor: pointer;
  color: green;
  font-size: 40%;
}

.cards {
  margin: 50px;
  padding: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  border-radius: 5em !important;
  background-color: #D5F3Fe;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card {

  margin: 20px;
  transition: 0.3s;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.button {
 /* Push the buttons to the bottom of the card container */
}
.card:hover {
  box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.2);
}

.btn {
  display: inline-block;
  padding: 1em 2.1em 1.1em;
  border-radius: 3px;
  margin: 8px;
  color: rgb(57, 55, 55);
  background-color: #fbdedb;
  background: #e74c3c;
  transition: 0.3s;
  font-family: sans-serif;
  font-weight: 800;
  font-size: 0.85em;
  text-transform: uppercase;
  text-align: right;
  text-decoration: none;
  box-shadow: 0em -0.3rem 0em rgba(0, 0, 0, 0.1) inset;
  position: relative;
}

.btn:hover,
.btn:focus {
  opacity: 0.8;
}

.btn:active {
  transform: scale(0.8);
}

.btn.circular {
  border-radius: 50em !important;
}

.orange {
  background: #eeb960;
}

.button {
  display: flex;
  justify-content: space-between;
}

.p {
  background-color: #66D3FA;
}

.App {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  justify-content: center;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background-color: #66D3FA;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.header {
	display: flex;
	align-items: center;
	justify-content: space-around;

	padding: 0 2rem;
	background-color: var(--mainColor);
	color: var(--textColor);
}


.header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
}

.header,
nav {
	display: flex;
  justify-content: space-around;
	align-items: center;
  padding: 5px;
}

@media only screen and (max-width: 1024px) {
  .card{
    margin: 10px;
  }
  .header .nav-btn {
    visibility: visible;
    opacity: 1;
    display: flex;
    align-items: right;
    padding: 12px;
  }

  .header nav {
    position: fixed;
    top: 10vh;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.2rem;
    background-color: #2b2a29;
    transition: 1s;
    z-index: 1000;
    overflow: hidden; /* Hide overflowing patterns */
  }

  .header .responsive_nav {
    transform: translateY(100vh);
  }

  nav .nav-close-btn {
    position: absolute;
    top: -1rem;
    right: 2rem;
  }

  /* Increase font size for navigation buttons */
  nav .menu {
    margin-bottom:20px;
    font-size: 20px; /* Adjust the font size to your preference */
  }
  nav::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: radial-gradient(
      circle,
      #3498db 10%,
      transparent 10%
    );
    background-size: 20px 20px; /* Adjust the size of the circles */
    animation: moveSmoothCircles 4s linear infinite; /* Adjust the animation duration */
  }

  @keyframes moveSmoothCircles {
    0%, 100% {
      transform: translate(0, 0);
    }
    25% {
      transform: translate(20px, -20px);
    }
    50% {
      transform: translate(-10px, 10px);
    }
    75% {
      transform: translate(10px, 20px);
    }
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}        
